import React, { Component } from 'react'
import { Link } from 'react-router'

import getUser from 'SRC/utils/getUser'

import CSSModules from 'react-css-modules'
import styles from './MainNav.css'

const _row = require('antd/lib/row')
const _col = require('antd/lib/col')
const _menu = require('antd/lib/menu')
const Modal = require('antd/lib/modal')
const Button = require('antd/lib/button')
const Input = require('antd/lib/input')
const MenuItem = _menu.Item

class MainNav extends Component {
  constructor(props) {
    super(props)
    this.success = this.success.bind(this)
    this.error = this.error.bind(this)
  }
  componentWillReceiveProps(nextProps) {
    if (!this.props.success && nextProps.success) {
      setTimeout(() => this.success(), 500)
    }
    if (!this.props.error && nextProps.error) {
      setTimeout(() => this.error(), 500)
    }
  }
  success() {
    document.getElementById('msgInput').value = ''
    Modal.success({
      title: '成功',
      content: '您的留言我们已经收到，感谢您的关注。'
    })
  }

  error() {
    Modal.error({
      title: '错误',
      content:
        <div>
          <p>由于网络或其他原因该留言未能提交，请稍后再试。</p>
          <p>感谢您的关注。</p>
        </div>
    })
  }
  render() {
    const setMsgBoard = this.props.setMsgBoard
    const isShowMsgBoard = this.props.isShowMsgBoard
    const isSubmitLoading = this.props.isSubmitLoading
    const activeItem = this.props.selectedKeys[0]
    const msgSubmit = this.props.msgSubmit
    return (
      <_row style={this.props.style} styleName="main-nav" type="flex" align="bottom">
        <_col span={3} styleName="main-nav-header">
          <div styleName="logo">
            <img src={require('SRC/assets/imgs/logo-white.png')} alt="logo" />
          </div>

        </_col>
        <_col span={3} style={{ height: '100%' }}>
          <div style={{ position: 'relative', top: '30%', fontSize: '18px', width: '120px', height: '100%' }}>
            用户画像平台
          </div>
        </_col>
        <_col span={14} style={{ paddingLeft: '5px' }}>
          <_menu selectedKeys={[`.$${activeItem}`]} mode="horizontal" styleName="main-nav-menu">
            {this.props.menuItems.map(menuItem => (
              <MenuItem key={menuItem.key} styleName={activeItem === menuItem.key ? 'menu-item-selected' : 'menu-item'}>
                <Link to={menuItem.link}>{menuItem.name}</Link>
              </MenuItem>
            ))}
          </_menu>
        </_col>
        <_col span={4} styleName="main-nav-footer">
          <div><span className="pointer" styleName="logout-btn" onClick={setMsgBoard}>留言</span><span>{getUser().name}</span><span className="pointer"><i className="fa fa-sign-out" ></i></span></div>
        </_col>
        <Modal
          title="欢迎留下你的建议和意见"
          visible={isShowMsgBoard}
          onOk={setMsgBoard}
          onCancel={setMsgBoard}
          footer={[
            <Button key="back" type="ghost" size="large" onClick={setMsgBoard}>
              返 回
            </Button>,
            <Button key="submit" type="primary" size="large" loading={isSubmitLoading} onClick={() => msgSubmit(document.getElementById('msgInput').value)}>
              提 交
            </Button>
          ]}
          >
          <Input
            id="msgInput"
            type="textarea"
            rows={4}
            />
        </Modal>
      </_row>
    )
  }
}

MainNav.propTypes = {
  menuItems: React.PropTypes.arrayOf(React.PropTypes.shape({
    key: React.PropTypes.string.isRequired,
    name: React.PropTypes.string.isRequired,
    link: React.PropTypes.string.isRequired
  })),
  selectedKeys: React.PropTypes.arrayOf(React.PropTypes.string),
  style: React.PropTypes.object,
  setMsgBoard: React.PropTypes.func,
  isSubmitLoading: React.PropTypes.bool,
  isShowMsgBoard: React.PropTypes.bool,
  msg: React.PropTypes.string,
  msgSubmit: React.PropTypes.func.isRequired,
  success: React.PropTypes.bool,
  error: React.PropTypes.bool
}

MainNav.defaultProps = {
  menuItems: [],
  selectedKeys: [],
  style: {}
}
export default CSSModules(MainNav, styles)



